<template>
    <div>
        <el-row>
            <headers :title='title'></headers>
        </el-row>
        <el-row>
            <el-col :span='18' :offset='3'>
                <div class="data_list">
                    <div class="data_l_t">
                        <span>数据列表</span>
                        <el-button type="primary" @click='ad' icon='el-icon-plus'>添加</el-button>
                        <el-dialog title="新增新闻类型" :visible.sync="dialogVisible" width="30%">
                            <el-form label-width='100px' ref="form" :model="form" :rules="rules">
                                <el-form-item label='新闻分类:' prop='news_type'>
                                    <el-input placeholder='输入新闻类型' id='inputs' v-model='form.news_type' maxlength='20'></el-input>
                                </el-form-item>
                            </el-form>
                            <span slot="footer" class="dialog-footer">
                                <el-button @click="dialogVisible = false">取 消</el-button>
                                <el-button type="primary" @click="add('form')">确 定</el-button>
                            </span>
                        </el-dialog>
                    </div>
                    <div>
                        <el-table :data="post_list.slice((currentPage-1)*pagesize,currentPage*pagesize)" style="width: 100%" :header-cell-style="rowClass" :cell-style="cellStyle" border @row-click='haedle'>
                            <el-table-column prop="id" label="序号" width="100">
                            </el-table-column>
                            <el-table-column prop="press_name" label="新闻版块">
                            </el-table-column>
                            <el-table-column prop="real_name" label="创建人" width="100">
                            </el-table-column>
                            <el-table-column prop="create_time" label="创建时间" width="200">
                            </el-table-column>
                            <el-table-column label="操作" width='150'>
                                <template scope='scope'>
                                    <span style='cursor: pointer;color:#0079FE;' @click.stop='del(scope.$index,scope.row.id)'><i class="el-icon-delete"></i> 删除</span>
                                </template>
                            </el-table-column>
                        </el-table>
                        <el-row>
                            <div class="pages">
                                <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="pagesize" layout="prev, pager, next, jumper" :total="total">
                                </el-pagination>
                            </div>
                        </el-row>
                    </div>
                    <el-dialog title="修改新闻类型" :visible.sync="dialogVisible2" width="30%">
                        <el-form label-width='100px' ref="form" :model="form" :rules="rules">
                            <el-form-item label='新闻分类:' prop='news_type'>
                                <el-input placeholder='输入新闻类型' id='inputs' v-model='form.news_type' maxlength='20'></el-input>
                            </el-form-item>
                        </el-form>
                        <span slot="footer" class="dialog-footer">
                            <el-button @click="dialogVisible2 = false">取 消</el-button>
                            <el-button type="primary" @click="add2('form')">确 定</el-button>
                        </span>
                    </el-dialog>
                </div>
            </el-col>
        </el-row>
    </div>
</template>
<script>
import headers from '../../components/header.vue'
export default {
    components: {
        headers
    },
    data() {
        return {
            title: '新闻版块',
            currentPage: 1,
            pagesize: 20,
            total: 1,
            post_list: [],
            dialogVisible: false,
            dialogVisible2: false,
            news_type: '',
            news_id: '',
            form: {
                news_type: ''
            },
            rules: {
                news_type: [{
                    required: true,
                    message: '请输入新闻类型',
                    trigger: 'blur'
                }, {
                    min: 0,
                    max: 20,
                    message: '最大为 20 个字符'
                }]
            }
        }
    },
    methods: {
        haedle(row, event, column) {
            console.log(row, event, column)
            this.dialogVisible2 = true
            this.form.news_type = row.press_name
            this.news_id = row.id
        },
        /*修改新闻版块*/
        add2(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    var id = window.sessionStorage.getItem('userId')
                    this.dialogVisible2 = false
                    this.$axios.post('/tomlive/pressType/updateByPrimaryKeySelective', this.$qs.stringify({
                        pressName: this.form.news_type,
                        id: this.news_id
                    })).then((res) => {
                        this.all()
                        this.form.news_type = ''
                    })
                } else {
                    console.log('error submit!!');
                    return false;
                }
            })
        },
        /*打开添加弹框*/
        ad() {
            this.dialogVisible = true
            setTimeout(() => {
                let inputelement = document.getElementById('inputs')
                inputelement.focus()
                this.form.news_type=''
            }, 1)
        },
        /*添加新闻版块*/
        add(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    var id = window.sessionStorage.getItem('userId')
                    this.dialogVisible = false
                    this.$axios.post('/tomlive/pressType/insertSelectProductType', this.$qs.stringify({
                        pressName: this.form.news_type,
                        userId: id
                    })).then((res) => {
                        this.all()
                        this.form.news_type = ''
                    })
                } else {
                    console.log('error submit!!');
                    return false;
                }
            })
        },
        del(index, id) {
            this.$confirm('是否删除?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then((action) => {
                if (action === 'confirm') {
                    this.$axios.post('/tomlive/pressType/deleteProductTypeByPrimaryKey', this.$qs.stringify({
                        id: id
                    })).then((res) => {
                        this.all()
                    })
                }
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消'
                });
            })
        },
        rowClass({ row, rowIndex }) {
            return "text-align:center;background:#f5f5f5;color:#666"
        },
        cellStyle({ row, column, rowIndex, columnIndex }) {
            return "text-align:center;"
        },
        handleSizeChange(val) {
            // console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            // console.log(`当前页: ${val}`);
        },
        /*查询所有新闻版块*/
        all() {
            this.$axios.post('/tomlive/pressType/selectAllPressType').then((res) => {
                this.post_list = res.data.info.reverse()
                this.total = this.post_list.length
                console.log(res)
            })
        }
    },
    mounted() {
        this.all()
    }
}
</script>
<style scoped>
.data_list {
    width: 100%;
    min-height: 800px;
    border: 1px solid #e9e9e9;
    box-sizing: border-box;
    background: #fff;
    margin: 20px 0;
    padding: 0px 20px;
}

.data_l_t {
    width: 100%;
    height: 60px;
    font-size: 14px;
    line-height: 60px;
    color: #666;
    font-weight: bold;
}

.data_l_t>button {
    float: right;
    margin: 10px 0;
}

.pages {
    margin: 20px 0 20px 0;
    float: right;
}
</style>
<style>
.el-dialog__header {
    width: 100%;
    height: 50px;
    background: #f5f5f5;
    padding: 0;
}

.el-dialog__header>span {
    margin-left: 20px;
    font-size: 16px;
}

.el-dialog__title {
    line-height: 50px;
}
</style>